.label {
  margin-top: 10px;
  color: #fff;
  font-size: 24px;
  text-align: center;
  font-weight: bold;
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.6);
}

.sharedContainer {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
  align-items: center;
  height: 90%;
  overflow-y: auto;
  padding: 20px;
  border-radius: 10px;
}

.textItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.colorContainer {
  display: flex;
  flex-direction: row;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: center;
}

.colorBlock {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  transition:
    transform 0.3s,
    box-shadow 0.3s;
}

.colorBlock:hover {
  transform: scale(1.1);
  box-shadow: 0 4px 10px rgba(255, 255, 255, 0.2);
}

.circle {
  font-size: 20px;
}

.black {
  color: black;
}

.white {
  color: white;
}

.red {
  color: red;
}

.orange {
  color: orange;
}

.yellow {
  color: yellow;
}

.green {
  color: green;
}

.blue {
  color: blue;
}

.purple {
  color: purple;
}

.selected {
  box-shadow: 0 0 10px rgba(185, 0, 255, 0.8);
}

.textPreview {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 15px;
  margin: 10px 0;
  border: 1px solid transparent;
  border-radius: 8px;
  background-color: #2c2c2c;
  color: white;
  cursor: pointer;
  transition:
    transform 0.3s,
    background-color 0.3s;
}

.textPreview:hover {
  background-color: #3e3e3e;
  transform: scale(1.02);
}

.selectedText {
  border: 2px solid rgba(185, 0, 255, 0.8);
  background-color: rgba(185, 0, 255, 0.1);
  transform: scale(1.05);
}

input[type='number'] {
  margin-left: 10px;
  padding: 5px;
  font-size: 18px;
  color: #fff;
  background-color: #1e1e1e;
  border: 1px solid #884f9f;
  border-radius: 5px;
  outline: none;
  width: 70px;
  text-align: center;
  transition: box-shadow 0.3s ease;
}

input[type='number']:hover,
input[type='number']:focus {
  box-shadow: 0 0 5px rgba(185, 0, 255, 0.6);
}

.deleteIcon {
  color: #e63946;
  transition:
    transform 0.3s ease,
    color 0.3s ease;
}

.deleteIcon:hover {
  transform: scale(1.2);
  color: #ff6f61;
}

.textItem input[type='text'] {
  width: 250px;
  padding: 10px;
  font-size: 18px;
  color: #fff;
  background: linear-gradient(135deg, #292929, #1e1e1e);
  border: 1px solid #884f9f;
  border-radius: 10px;
  outline: none;
  transition:
    box-shadow 0.3s ease,
    transform 0.3s ease;
}

.textItem input[type='text']:hover,
.textItem input[type='text']:focus {
  box-shadow: 0 0 8px rgba(185, 0, 255, 0.8);
  transform: scale(1.05);
}

@media (min-width: 1025px) and (max-width: 1280px) {
}

@media (min-width: 1281px) and (max-width: 1440px) {
  .label {
    margin-top: 5px;
    font-size: 15px;
  }

  .sharedContainer {
    gap: 10px;
    margin-top: 20px;
    height: 90%;
    padding: 5px;
  }

  .textItem {
    gap: 5px;
  }

  .colorContainer {
    gap: 10px;
  }

  .colorBlock {
    width: 30px;
    height: 30px;
  }

  .circle {
    font-size: 10px;
  }

  input[type='number'] {
    margin-left: 10px;
    padding: 5px;
    font-size: 18px;
    width: 70px;
  }

  .textItem input[type='text'] {
    width: 200px;
    padding: 10px;
    font-size: 18px;
  }
}

